<template>
    <!--这是评论展示区-->
<div class="evaluation">
    <el-row>
        <el-col :span="4">&nbsp</el-col>
        <el-col :span="16">
            <!-- 游戏信息 -->
            <top></top>
            <!-- 游戏评论 -->
            <font></font>
            <!-- 右边筛选功能 -->
            <right></right>
        </el-col>
        <el-col :span="4">&nbsp</el-col>
    </el-row>
</div>

</template>

<script>
import top from '../components2/top'
import right from '../components2/right'
import font from '../components2/font'

export default {
    data() {
        return {
          //头像
          circleUrl:'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png',
          inputfont:''
        }
    },
    components:{
        top,font,right
    },
    methods: {
    go() {
      this.$parent.goperson();
    },
    goevaluation() {
      this.$router.replace('/evaluation');
    },
    gotoindex() {
        this.$router.replace('/');
        this.$emit('goindex')
    }
  }
}
</script>

<style scoped>
.evaluation {
    background-color: cadetblue;
    width: 100%;
    float:left;
}
/*这是上导航栏 */
    .hello {
    height:10%;
    width: 100%;
    background-color:#777777;
    margin: 0px;
    }
    

</style>


