<template>
    <div class="font">
        我是font
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return {

        }
    },
    methods: {},
        //get请求不传参，打印data
    created:function() {
        //$axios.defaults.baseURL='localhost:8889/';
        axios.get('localhost:8889/game/168332/main')
        //成功
        .then(function(ret){console.log(ret.data)
            })
        //失败
         .catch()
       }
        /*get请求传参id=123，打印data
        created:function() {
            axios.defaults.baseURL='localhost:8889/';
            axios.get('game/168332/main?id=123')
            .then(ret=>{console.log(ret.data)})
        }
        */
}
</script>

<style scoped>
.font {
    width: 70%;
    float: left;
    height: 800px;
    background-color:#fff;
}

</style>


