<template>
    <div class="right">
        <h3>共筛选出{{ sum }}条信息</h3>
        <div style="background-color:#d6cece;height:20px;width:100%;margin: 20px;margin-left:0px;">单选</div>
         <!--单选框-->
         <el-radio v-model="radio" label="所有评价" >所有评价</el-radio><hr>
        <el-radio v-model="radio" label="所有好评" >所有好评</el-radio><hr>
        <el-radio v-model="radio" label="所有差评">所有差评</el-radio><hr>
        <el-radio v-model="radio" label="长篇好评">长篇好评</el-radio><hr>
        <el-radio v-model="radio" label="长篇差评">长篇差评</el-radio>
        <div style="background-color:#d6cece;height:20px;width:100%;margin: 20px;margin-left:0px;">多选</div>
        <!--多选框,选中既压入数组-->
        <el-checkbox-group v-model="checkList" >
        <el-checkbox  label="VIP 用户" ></el-checkbox><hr>
        <el-checkbox  label="路人评价" ></el-checkbox><hr>
        <el-checkbox  label="带图评价" ></el-checkbox><hr>
        <el-checkbox  label="最近30天" style="margin-right:30px;"></el-checkbox>
        </el-checkbox-group>
        <div style="background-color:#d6cece;height:20px;width:100%;margin: 20px;margin-left:0px;">数据分析</div>
        <!--数据可视化-->
        <el-button type="primary" >
            <i class="el-icon-s-data" @click="$router.push('/analyse')">&nbsp&nbsp图表分析&nbsp》》</i>
        </el-button>
    </div>
</template>

<script>
import store from '../vuex/store'

export default {
    data() {
        return {
            radio:'所有评价',
            checkList:[],
            //评论条数
            sum:0
        };
    },
    methods: {
    },
    store
}
</script>

<style scoped>
.right {
    width: 30%;
    float: left;
    height: 800px;
    background-color:white;
}
hr {
    background-color:rgb(233, 210, 210);
    border: none;
    height: 1px;
    margin: 20px;
}


</style>


