<template>
    <div class="select1">
        <el-row>
            <el-col :span="4">&nbsp</el-col>
            <!-- 主要展示区 -->
            <el-col :span="16" style="background-color:white;">
                <div <div v-for="item in game" :key="item.id" class="box" @click="aa(item)">
                    图片--{{ item.name }}---游戏id{{item.id}}--获取的id{{gameid}}
                    <el-button >关注</el-button>
                </div>
            </el-col>
            <el-col :span="4">&nbsp</el-col>
        </el-row>
    </div>
</template>

<script>
import store from '@/vuex/store'
import {mapState} from 'vuex'

export default {
    data() {
        return{
            //这是写死的，需要后端传
            
            game:[
                {id:1,name:'英雄联盟 LOL',intr:'这是英雄联盟的介绍',src:require("../../images/评论区页面/lol.jpg")},
                {id:3,name:'QQ飞车',intr:'这是QQ飞车的介绍',src:require("../../images/评论区页面/QQ飞车.jpg")},
                {id:168332,name:'原神',intr:'这是原神的介绍',src:require("../../images/评论区页面/原神.jpg")}
            ]
        }
    },
    methods:{
        //点击游戏触发事件
        aa(item) {
            
            //把该游戏的id存到仓库里。成功
            this.$store.commit('goevaluation', item);
            this.$router.push('/evaluation');
        }
    },
    computed:mapState(['gameid']),
    store
    
}
</script>

<style scoped>
.box {
    height: 100px;
    width: 100%;
    margin-bottom: 5px;
    border-bottom: 1px solid black;
}
.select1 {
    background-color: cadetblue;
    height:90%;
    width: 100%;
}
</style>


